import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '*',
            redirect: '/'
        },
        {
            path: '/',
            name: '_home',
            redirect: '/Home',
        },
        {
            path: '/Home',
            name: 'Home',
            component:  resolve => require(['@/view/wutuoban/index'], resolve),
        },
        {
            path: '/Faq',
            name: 'Faq',
            component:  resolve => require(['@/view/wutuoban/faq'], resolve),
        },
        {
            path: '/OutRoadmap',
            name: 'OutRoadmap',
            component:  resolve => require(['@/view/wutuoban/outRoadmap'], resolve),
        },
        {
            path: '/Citys',
            name: 'Citys',
            component:  resolve => require(['@/view/wutuoban/citys'], resolve),
        }
    ]
})



