<template>
    <transition name="fade">
        <div class="toast" v-show="show">
            {{message}}
        </div>

    </transition>
</template>

<script>
    export default {
        data() {
            return {
                show: false,
                message: ""
            };
        }
    };
</script>

<style scoped>
    .toast {
        position: fixed;
        top: 40%;
        left: 50%;
        margin-left: -10vw;
        padding: 2vw;
        width: 30%;
        height: auto;
        font-size: 4vw;
        color: #fff;
        text-align: center;
        background-color: rgba(0, 0, 0, 0.8);
        border-radius: 2vw;
        z-index: 3000;
    }

    .fade-enter-active,
    .fade-leave-active {
        transition: 0.3s ease-out;
    }
    .fade-enter {
        opacity: 0;
        transform: scale(1.2);
    }
    .fade-leave-to {
        opacity: 0;
        transform: scale(0.8);
    }
</style>



