import Vue from 'vue';
import VueI18n from 'vue-i18n';
import Cookies from 'js-cookie';
import zhLocale from './zh';
import enLocale from './en';
import deLocale from './de';
import elLocale from './el';
import esLocale from './es';
import jpLocale from './jp';
import krLocale from './kr';
import ruLocale from './ru';
import frLocale from './fr';
import plLocale from './pl';


Vue.use(VueI18n);

// let lang = (navigator.language || navigator.browserLanguage).toLowerCase() === 'zh-cn' ? 'zh' : 'en';

let lang = sessionStorage.getItem('lang');
if(!lang){
    // lang  = (navigator.language || navigator.browserLanguage).toLowerCase() === 'zh-cn' ? 'zh' : 'en';
    lang =  'en';
    sessionStorage.setItem('lang', lang);
}

const messages = {
    en: {
        ...enLocale,
        // ...elementEnLocale
    },
    zh: {
        ...zhLocale,
        // ...elementZhLocale
    },
    de: {
        ...deLocale,
        // ...elementEnLocale
    },
    el: {
        ...elLocale,
        // ...elementEnLocale
    },
    es: {
        ...esLocale,
        // ...elementEnLocale
    },
    fr: {
        ...frLocale,
        // ...elementEnLocale
    },
    jp: {
        ...jpLocale,
        // ...elementEnLocale
    },
    kr: {
        ...krLocale,
        // ...elementEnLocale
    },
    pl: {
        ...plLocale,
        // ...elementEnLocale
    },
    ru: {
        ...ruLocale,
        // ...elementEnLocale
    },
};

const i18n = new VueI18n({
    locale: lang,
    messages // set locale messages
});

export default i18n;



